import React from 'react'
import {FaRegComment, FaRetweet,FaRegHeart} from 'react-icons/fa'
import {FiUpload} from 'react-icons/fi'

const Tweet = (props) => {
    return (
        <div className='tweet_card'>
            <img src={props.proimg} />
            <div className="tweet_card_body">
                <div className="tweet_id">
                    <span>{props.name}</span>
                    <span>@{props.id}</span>
                </div>
                <div className="tweet_content">
                    <p>{props.tbody}</p>
                    <img src={props.image} />
                    <div className="tweet_body_icon">
                        <div><FaRegComment className='icon'/> 10</div>
                        <div><FaRetweet className='icon'/> 60</div>
                        <div><FaRegHeart className='icon'/> 160</div>
                        <div><FiUpload className='icon'/></div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Tweet
