import React, { useState, useRef } from "react";
import {
  FaStarOfLife,
  FaRegImages,
  FaPlus,
  FaRegSmile,
  FaCalendarTimes,
  FaGlobeAmericas,
} from "react-icons/fa";
import { RiFileGifLine } from "react-icons/ri";
import { ImStatsBars } from "react-icons/im";
import profile from "../sul.jpg";
import Tweet from "./Tweet";
import tweetData from "../TweetData";

const Tweetbar = () => {
  const inputRef = useRef(null);
  const [showptag, setshowptag] = useState(false);
  function handleClick() {
    if (!showptag) {
      setshowptag(true);
    } else {
      setshowptag(false);
    }
  }
  return (
    <div className="tweetbar">
      <div className="heading">
        <h2>Home</h2>
        <p>
          <FaStarOfLife />
        </p>
      </div>
      <div id="tweet_box">
        {showptag ? (
          <span id="input_text">
            <FaGlobeAmericas />
            <p>Everyone can reply</p>
          </span>
        ) : (
          ""
        )}
        <div>
          <img src={profile} alt="" />
          <textarea
            rows="6"
            placeholder="What's happening?"
            onClick={handleClick}
            ref={inputRef}
          ></textarea>
        </div>
        <div className="tweet_bottom">
          <div className="icon">
            <span>
              <FaRegImages />
            </span>
            <span>
              <RiFileGifLine />
            </span>
            <span>
              <ImStatsBars className="imstartbar" />
            </span>
            <span>
              <FaRegSmile />
            </span>
            <span>
              <FaCalendarTimes />
            </span>
          </div>
          <div id="tweet_btn">
            <p>
              <FaPlus className="tweet_plus" />
            </p>
            <button>Tweet</button>
          </div>
        </div>
      </div>
      {/* start Tweet */}
      <div id="tweet_feed">
        {tweetData.map((tw, index) => {
          return <Tweet key={index}
          proimg={tw.proimg}
          id={tw.id}
          name={tw.name}
          image={tw.tweetImage}
          tbody={tw.tweet_body}
        />;
        })}
      </div>
    </div>
  );
};

export default Tweetbar;
