import React from "react";

const TrendFeed = (props) => {
  return (
    <>
      <div className="card">
        <div className="card_text">
          <p style={{ marginBottom: "5px", color: "#536471" }}>
            {props.name} <span>.LIVE</span>
          </p>
          <p>
            <b>{props.name}:</b> {props.text}
          </p>
        </div>
        <img src={props.img} />
      </div>
    </>
  );
};

export default TrendFeed;
