import React from 'react'
import proimg from '../sul.jpg'
import {FaTwitter,FaHome,FaHashtag,FaRegBell,FaEnvelope,FaRegBookmark,FaRegListAlt,FaRegUser} from 'react-icons/fa'

const Sidebar = () => {
    return (
        <div className="sidebar">
            <ul>
                
                <li><FaTwitter className='logo' /></li>
                <li>
                    <a href='#'><FaHome className='icon' /> Home</a>
                </li>
                <li>
                    <a href='#'><FaHashtag className='icon' />Explore</a>
                </li>
                <li>
                    <a href='#'><FaRegBell className='icon' />Notifications</a>
                </li>
                <li>
                    <a href='#'><FaEnvelope className='icon' />Messages</a>
                </li>
                <li>
                    <a href='#'><FaRegBookmark className='icon' />Bookmarks</a>
                </li>
                <li>
                    <a href='#'><FaRegListAlt className='icon' />List</a>
                </li>
                <li>
                    <a href='#'><FaRegUser className='icon' />Profile</a>
                </li>
                <li>
                    <a href='#'><div className='more'><span></span><span></span><span></span></div>More</a>
                </li>
                <li>
                    <button>More</button>
                </li>
                <li>
                    <a href='#' className="twitter_id"><img src={proimg}/> <div><h4>SULEMAN RAZA</h4><p>@sulemanraza_34</p></div></a>
                </li>
           
            </ul>
        </div>
    )
}

export default Sidebar
